<!--
 * @Author: Marlon
 * @Date: 2020-02-21 08:28:50
 * @LastEditors: Marlon
 * @LastEditTime: 2020-02-21 10:50:08
 * @Description: 元数据-标准匹配详情
 -->
<template>
  <div class="data-detail">
    <div class="data-detail-top">
      <ul class="area-query">
        <li>
          <span>元数据：</span>
          <span>南京</span>
          <span>ods</span>
        </li>
        <li class="area-title-1">
          <span>数据标准：</span>
          <span>南京数据标准V1.</span>
        </li>
        <li>
          <label for>匹配情况过滤：</label>
          <el-select v-model="proVal" filterable placeholder="请选择" size="small">
            <el-option
              v-for="item in proList"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </li>
      </ul>
      <div class="area-table">
        <TableDefault
          TId="1"
          :TTitle="tableTitle"
          :TData="tableData"
          :TIsShowPage="true"
          :TEdit="TEdit"
          @editBtnCb="editBtnCb"
        ></TableDefault>
      </div>
    </div>
    <span class="data-detail-mid" @click="handleFold">
      <i v-show="fold" class="el-icon-arrow-down up-dow"></i>
      <i v-show="!fold" class="el-icon-arrow-up up-dow"></i>
    </span>
    <div v-show="fold" class="data-detail-bot">
      <ul class="area-query">
        <li>
          <span>元数据表</span>
          <span>“ce_section 标段表”</span>
        </li>
        <li class="area-title-2">
          <span>数据标准数据集</span>
          <span>“ce_section 标段表”</span>
        </li>
        <li>
          <label for>匹配情况过滤：</label>
          <el-select v-model="proVal" filterable placeholder="请选择" size="small">
            <el-option
              v-for="item in proList"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </li>
      </ul>
      <p class="area-title">字段与属性匹配详情：</p>
      <div class="bot-table">
        <TableDefault
          TId="2"
          :TTitle="tableTitleBot"
          :TData="tableDataBot"
          :TIsShowPage="true"
          :TEdit="TEditBot"
          @editBtnCb="editBtnCb"
        ></TableDefault>
      </div>
    </div>
    <!-- 弹出框 -->
    <FormDialog
      :FDVisible="FDVisible"
      :FDTitle="FDTitle"
      FDWidth="500"
      :FDForm="FDForm"
      @editBtnCb="editBtnCbDiglog"
    ></FormDialog>
  </div>
</template>

<script>
import mixin from "mixin";

export default {
  mixins: [mixin],
  data() {
    return {
      fold: true,
      //   项目名称
      proVal: "",
      //   项目列表
      proList: [
        {
          value: "",
          label: "所有项目"
        },
        {
          value: "选项2",
          label: "通用"
        },
        {
          value: "选项3",
          label: "南京"
        },
        {
          value: "选项4",
          label: "杭州"
        },
        {
          value: "选项5",
          label: "济南"
        }
      ],
      // 编辑-表格配置
      TEdit: {
        show: true,
        title: "操作",
        editTypes: [
          {
            label: "选择数据集"
          }
        ]
      },
      TEditBot: {
        show: true,
        title: "操作",
        editTypes: [
          {
            label: "选择数据标准属性"
          }
        ]
      },
      tableTitle: [
        {
          prop: "name",
          label: "所属项目"
        },
        {
          prop: "date",
          label: "共维护更新次数"
        },
        {
          prop: "address",
          label: "对应数据源(物理库)"
        },
        {
          prop: "address",
          label: "对应数据源(物理库)"
        }
      ],
      tableData: [
        {
          date: "122",
          name: "通用",
          address: "45"
        },
        {
          date: "122",
          name: "海淀",
          address: "45"
        },
        {
          date: "122",
          name: "山西",
          address: "45"
        },
        {
          date: "122",
          name: "济南",
          address: "45"
        },
        {
          date: "122",
          name: "山东",
          address: "45"
        },
        {
          date: "122",
          name: "贵州",
          address: "45"
        },
        {
          date: "122",
          name: "福州",
          address: "45"
        },
        {
          date: "122",
          name: "泰安",
          address: "45"
        },
        {
          date: "122",
          name: "南京",
          address: "45"
        },
        {
          date: "122",
          name: "广州",
          address: "45"
        }
      ],
      tableTitleBot: [
        {
          prop: "name",
          label: "更新SQL"
        },
        {
          prop: "date",
          label: "维护时间"
        },
        {
          prop: "address",
          label: "负责人"
        }
      ],
      tableDataBot: [
        {
          date: "2020-02-02 02:02:02",
          name: "SQL",
          address: "Marlon"
        },
        {
          date: "2020-02-02 02:02:02",
          name: "SQL",
          address: "Marlon"
        },
        {
          date: "2020-02-02 02:02:02",
          name: "SQL",
          address: "Marlon"
        },
        {
          date: "2020-02-02 02:02:02",
          name: "SQL",
          address: "Marlon"
        },
        {
          date: "2020-02-02 02:02:02",
          name: "SQL",
          address: "Marlon"
        },
        {
          date: "2020-02-02 02:02:02",
          name: "SQL",
          address: "Marlon"
        },
        {
          date: "2020-02-02 02:02:02",
          name: "SQL",
          address: "Marlon"
        },
        {
          date: "2020-02-02 02:02:02",
          name: "SQL",
          address: "Marlon"
        },
        {
          date: "2020-02-02 02:02:02",
          name: "SQL",
          address: "Marlon"
        },
        {
          date: "2020-02-02 02:02:02",
          name: "SQL",
          address: "Marlon"
        }
      ],
      // 是否显示弹出框
      FDVisible: false,
      FDTitle: "",
      // 弹出框配置
      FDForm: [
        {
          type: "select",
          label: {
            name: "所属项目",
            width: "120"
          },
          width: "200",
          defaultModel: "",
          placeholder: "",
          option: [
            {
              label: "海淀",
              value: "haidian"
            }
          ]
        },
        {
          type: "input",
          label: {
            name: "中文名称",
            width: "120"
          },
          width: "280",
          defaultModel: "",
          placeholder: ""
        },
        {
          type: "input",
          label: {
            name: "库名",
            width: "120"
          },
          width: "280",
          defaultModel: "",
          placeholder: ""
        },
        {
          type: "textarea",
          label: {
            name: "备注",
            width: "120"
          },
          width: "280",
          defaultModel: "",
          placeholder: "",
          autosize: {
            min: 2,
            max: 4
          }
        }
      ]
    };
  },
  methods: {
    handleFold() {
      this.fold = !this.fold;
    },
    // 表格-回调
    editBtnCb(editItem) {
      console.log(editItem);
      let text = editItem.label;
      switch (text) {
        case "选择数据集":
          this.showDialog({ type: text });
          return;
        case "选择数据标准属性":
            this.showDialog({ type: text });
            return
        default:
          break;
      }
    },
    // 显示弹出框并修改配置
    showDialog({ type }) {
      switch (type) {
        case "选择数据集":
          this.FDTitle = "选择数据集";
          this.FDVisible = true;
          this.FDForm = [
            {
              type: "select",
              label: {
                name: "数据集",
                width: "120"
              },
              width: "200",
              defaultModel: "",
              placeholder: "",
              option: [
                {
                  label: "海淀_adc",
                  value: "haidian"
                },
                {
                  label: "海淀_snc",
                  value: "haidian"
                }
              ]
            }
          ];
          return;
        case "选择数据标准属性":
          this.FDTitle = "选择数据标准属性";
          this.FDVisible = true;
          this.FDForm = [
            {
              type: "select",
              label: {
                name: "数据标准属性",
                width: "120"
              },
              width: "200",
              defaultModel: "",
              placeholder: "",
              option: [
                {
                  label: "海淀_adc",
                  value: "haidian"
                },
                {
                  label: "海淀_snc",
                  value: "haidian"
                }
              ]
            }
          ];
          return;
        default:
          return;
      }
    },
    // 弹出框-回调
    editBtnCbDiglog(obj, formReq) {
      this.FDVisible = false;
      if (obj === "确定") {
        // console.log(this.FDTitle);
        switch (this.FDTitle) {
          case "新增库":
            // TODO 发请求
            console.log(formReq);
            return;

          default:
            return;
        }
      }
    },
  }
};
</script>

<style lang="scss" scoped>
.data-detail {
  padding: 20px;
  min-width: 1300px;
  width: calc(100% - 1px);
  height: 100%;
  box-sizing: border-box;
  border: 1px solid red;
  overflow: auto;
  display: flex;
  flex-direction: column;
  .area-query {
    float: left;
    width: 100%;
    overflow: hidden;
    li {
      float: left;
      height: 32px;
      line-height: 30px;
      &:first-child {
        float: left;
        margin-left: 30px;
      }
      &:last-child {
        float: right;
        margin-right: 30px;
      }
    }
  }
  .area {
    &-title-1 {
      margin-left: 300px;
    }
    &-title-2 {
      margin-left: 80px;
    }
    &-title {
      float: left;
      height: 32px;
      line-height: 30px;
      margin: 5px 30px 0;
    }
  }
  &-top {
    width: 100%;
    height: 100px;
    box-sizing: border-box;
    border: 1px solid red;
    flex: 1;
    overflow: auto;
    .area-table {
      margin-top: 40px;
      width: 100%;
      height: calc(100% - 40px);
      overflow: auto;
    }
  }
  &-mid {
    display: inline-block;
    height: 30px;
    width: 100%;
    box-sizing: border-box;
    border: 1px solid red;
    text-align: center;
    .up-dow {
      font-weight: bolder;
      font-size: 30px;
    }
  }
  &-bot {
    width: 100%;
    height: 100px;
    box-sizing: border-box;
    border: 1px solid red;
    flex: 1;
    overflow: auto;
    .bot-table {
      margin-top: 40px;
      width: 100%;
      height: calc(100% - 75px);
      overflow: auto;
    }
  }
}
</style>


